.homeSection {
  min-height: 250px !important;
  min-width: 100vw;
}

.particles {
  background-image: linear-gradient(to top right, #3629a7, #343a40);
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}

.intro {
  font-size: 32pt;
  line-height: 36pt;
  text-align: center;
  width: fit-content;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.line {
  width: 50vw;
  background-color: #BBD336;
  height: 3px;
  padding: 0;
  margin: auto;
  @media screen and (max-width: 1000px) {
    width: 62vw;
  }
  @media screen and (max-width: 800px) {
    width: 87vw;
  }
}


.heroText {
  @media screen and (max-height: 799px) {
    display: flex;
    flex-direction: column-reverse;
  }
  @media screen and (max-width: 1400px) {
    display: flex;
    flex-direction: column-reverse;
  }
  @media screen and (min-height: 800px) and (min-width: 1400px) {
    margin-top: 5%;
  }
  .heroFullName {
    color: #ea2c62;
  }

  .heroTitle {
    color: #d37815;
  }

  h1 {
    margin: 10px 0 20px 0;
    letter-spacing: -1px;
    font-family: Playfair Display, serif;
    font-weight: 700;
    font-size: 3.5vw;
    line-height: 1;
    text-align: center;
    color: #fff;

    @media screen and (max-width: 1000px) {
      font-size: 4.5vw;
    }

    @media screen and (max-width: 800px) {
      font-size: 6vw;
    }
  }
}

.section {
  min-height: 100vh;
  width: 100vw;
  position: relative;
  z-index: 2;


  .whiteMask {
    bottom: 0;
    position: absolute;
    z-index: 1;
    left: 50%;
    min-width: 100%;
    height: 400px;
    max-height: 40%;
    display: none;
    transform: translateX(-50%);
    @media screen and (max-width: 800px) {
      height: 100px;
    }
    @media screen and (min-width: 500px) and (min-height: 500px) {
      display: block;
    }
  }

  > div {
    position: absolute;
    top: 60px;
    left: 0;
    z-index: 300;
    display: flex;
    flex-direction: column;
    align-items: center;
    //justify-content: center;

    width: 100vw;
    height: 100vh;
    min-height: 250px !important;


    @media screen and (max-width: 450px) {
      justify-content: center;

    }
  }
}


.highlight {
  color: #e31b6d;
  font-family: "Raleway-semibold";
}

.welcomeTo {
  width: fit-content;
  height: fit-content;
  display: none;
  @media screen and (min-width: 600px) and (min-height: 600px) {
    display: block;
  }
}


.contact {
  z-index: 13;
  color: white;
  display: flex;
  flex-direction: column;
  margin: auto;

  >:nth-child(1),
  >:nth-child(2),
  >:nth-child(3),
  >:nth-child(4) {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
  }
  img {
    width: 50px;
    height: 50px;
    margin: 0 10px;
  }
}

.link {
  color: white;
  font-weight: bold;
  text-decoration: none;

  &:hover {
    cursor: pointer;
    text-decoration: underline !important;
    color: #e31b6d;
  }
}
