.aboutSection{
    min-width: 100vw;
    background-color: white;
    z-index: 3;
    position: relative;
    display: flex;
    justify-content: center;
    flex-direction: column;

    .shifuImg {
        margin: 0 auto;
        @media screen and (min-width: 1200px) {
            position: absolute;
            top: 120px;
            right: 20px;
        }
        @media screen and (max-width: 470px) {
            width: 90%;
        }
    }
    >:nth-child(2) {
        width: 40px;
        height: 2px;
        background-color: #ef4b6c;
        margin: 2em auto;
    }

    >h1 {
        margin: 75px 0 0 0;
        letter-spacing: -1px;
        font-family: Playfair Display, serif;
        font-weight: 700;
        font-size: 40px;
        line-height: 1;
        text-align: center;
        color: #333;
        position: relative;
    }


    .services {
        display: flex;
        flex-direction: row;
        flex-flow: wrap;
        justify-content: center;
        margin: 0 auto;
        width: fit-content;
    }
    .service {
        width: 138px;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 0 0.8em;

        >:first-child {
            border-radius: 50%;
            width: 80px;
            height: 80px;
        }
        >:nth-child(2) {
            text-align: center;
            h4 {
                margin: 0;
                font-size: 16px;
                line-height: 1.22;
                font-weight: 500;
                color: #1b2025;
            }
        }
    }

    .floatingTech {
        position: relative;
        width: 400px;
        @media screen and (max-width: 750px) {
            width: 90%;
        }
    }

    .whoisthisguy {
        padding: 20px;
        width: 500px;
        //border-left: 1px solid #cccccc;
        @media screen and (max-width: 600px) {
            width: 90%;
        }
        >h3 {
            margin-top: 0;
        }

        p {
            color: #757c98;
            margin-bottom: 1em;
        }

        .link {
            color: #e31b6d;
            text-decoration: none;

            &:hover {
                text-decoration: underline !important;
                color: adjust-color(#e31b6d, $lightness: 20%, $alpha: 0.8);
            }
        }

    }

    .info {
        display: flex;
        flex-direction: row;
        flex-flow: wrap;
        justify-content: center;
        //border: 1px solid #cccccc;
        //border-radius: 15px;
        width: fit-content;
        margin: 0 auto;
    }
}

.tasks {
    font-size: 16px;
    padding: 0 0 0 5px;

    li {
        list-style-type: circle;
        margin-bottom: 6px;
        list-style-position: inside;
        font-family: Lora,serif;
        font-size: 12pt;
        line-height: 1.73;
        letter-spacing: .5px;
        color: #626881;
    }
}
